import React from 'react'
import { Layout } from "../components"
import * as styles from '.././styles/about.module.css'

export default function AboutPage() {
  return(
    <Layout>
      <section className={styles.container}>
        <div className={styles.paragraph}>
          I studied web design and programming in
          high school and developed web sites and applications.
        </div>
        <div className={styles.software}>
          <h3 className="w-full text-2xl font-bold">Software / Tools</h3>
          <ul className="flex flex-wrap gap-2 flex-col md:h-60">
            <li>Figma</li>
            <li>VS Code</li>
            <li>Terminal</li>
            <li>Codepen</li>
            <li>Github</li>
            <li>Jenkins</li>
          </ul>
        </div>
        <div className={styles.development}>
          <h3 className="w-full text-2xl font-bold">Development</h3>
          <ul className="flex flex-wrap gap-2 flex-col md:h-60 h-80">
            <li>HTML</li>
            <li className="">CSS</li>
            <li>Javascript</li>
            <li>TypeScript</li>
            <li>React JS</li>
            <li>Node JS</li>
            <li>GraphQL</li>
            <li>MongoDB</li>
            <li>Yarn</li>
            <li>NPM</li>
            <li>Git</li>
            <li>Kubernetes</li>
            <li>Lambda</li>
            <li>Docker</li>
            <li>ECS</li>
            <li>EC2</li>
            <li>Serverless</li>
          </ul>
        </div>
      </section>
    </Layout>
  )
}